<template>
  <div>
    <!--begin::Notice-->
    <div
      class="alert alert-custom alert-white alert-shadow gutter-b mt-1"
      role="alert"
    >
      <div class="alert-icon alert-icon-top">
        <div>
          <img
            style="height: 48px"
            :src="
              'media/logos/task_logo/' +
              this.$store.state.taskinfo.name +
              '.jpg'
            "
          />
        </div>
        <!--end::Svg Icon-->
      </div>

      <div class="alert-text alert-text-center">
        <p style="margin-top: 15px">
          <span
            class="label label-inline label-pill label-primary label-rounded mr-2"
          >
            DESCRIPTION:
          </span>
          {{ this.$store.state.taskinfo.description }}
        </p>
      </div>
    </div>
    <!--end::Notice-->

    <!--start Tabs -->
    <div class="card card-custom gutter-b">
      <b-tabs card>
        <b-tab title="SOTA" active>
          <WidgetDataset :SOTA_infos="this.dataset_json"></WidgetDataset>
        </b-tab>
        <b-tab v-on:click="setResultsTab" title="Results">
          <component
            v-bind:is="show_component"
            keep-alive
            :dataset_list="dataset_list"
            :metric_list="metric_list"
            :available_trans_list="available_trans_list"
          ></component>
        </b-tab>
        <b-tab title="Transformations">
          <WidgetTransformations></WidgetTransformations>
        </b-tab>
        <b-tab title="papers">
          <WidgetPaperList></WidgetPaperList>
        </b-tab>
        <!-- <b-tab title="Subpopulation"> </b-tab> -->
      </b-tabs>
    </div>
    <!--end::Tab content-->
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";
import { SET_TASKINFO } from "@/core/services/store/taskinfo.module";
const WidgetDataset = (resolve) =>
  require(["@/view/content/widgets/list/widget-dataset.vue"], resolve);
const WidgetTransformations = (resolve) =>
  require(["@/view/content/widgets/list/widget-transformations.vue"], resolve);
const WidgetResults = (resolve) =>
  require(["@/view/content/widgets/list/widget-results.vue"], resolve);
const WidgetPaperList = (resolve) =>
  require(["@/view/content/widgets/list/widget-paperlist.vue"], resolve);

// import WidgetDataset from "@/view/content/widgets/list/widget-dataset.vue";
// import WidgetTransformations from "@/view/content/widgets/list/widget-transformations.vue";
// import WidgetResults from "@/view/content/widgets/list/widget-results.vue";

export default {
  name: "builder",
  components: {
    WidgetDataset,
    WidgetTransformations,
    WidgetResults,
    WidgetPaperList
  },

  beforeCreate() {
    // show page loading
    // this.$store.dispatch(ADD_BODY_CLASSNAME, "page-loading");
    // initialize html element classes
  },
  created() {
    var desc_json = require("../../../public/Tasks/" +
      this.$route.query.task +
      "/task_description.json");
    this.$store.dispatch(SET_TASKINFO, desc_json);
  },

  data() {
    var dataset_json = require("../../../public/Tasks/" +
      this.$route.query.task +
      "/dataset_description.json");

    // metric 二维列表
    var dataset_list = new Array();
    var metric_list = new Array();
    var available_trans_list = new Array();
    for (var index in dataset_json) {
      dataset_list.push(dataset_json[index].name);
      available_trans_list.push(
        dataset_json[index].available_transformation_type
      );
      metric_list.push([]);
      for (var metric in dataset_json[index].models[0].metric) {
        if (metric_list[index].indexOf(metric) == -1) {
          metric_list[[index]].push(metric);
        }
      }
    }
    return {
      show_component: "",
      dataset_list: dataset_list,
      dataset_json: dataset_json,
      metric_list: metric_list,
      available_trans_list: available_trans_list,
      tabIndex: 0,
    };
  },

  computed: {
    ...mapGetters(["layoutConfig"]),
    loaderLogo() {
      return process.env.BASE_URL + this.layoutConfig("loader.logo");
    },
  },
  mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: this.$store.state.taskinfo.full_name },
    ]);
    // setTimeout(() => {
    //   // Remove page loader after some time
    //   this.$store.dispatch(REMOVE_BODY_CLASSNAME, "page-loading");
    // }, 600);
  },
  destroyed() {
    localStorage.removeItem("builderTab");
  },
  methods: {
    /**
     * Reset config
     */
    reset(event) {
      event.preventDefault();
      // remove existing saved config
      localStorage.removeItem("config");
      window.location.reload();
    },

    /**
     * Check if the page loader is enabled
     * @returns {boolean}
     */
    loaderEnabled() {
      return !/false/.test(this.layoutConfig("loader.type"));
    },
    /**
     * Set previous tab active
     */
    setActivePreviousTab() {
      this.tabIndex = parseInt(localStorage.getItem("builderTab")) || 0;
      const links = this.$refs["builder-tab"].querySelectorAll(".nav-link");
      // remove active tab links
      for (let i = 0; i < links.length; i++) {
        links[i].classList.remove("active");
      }
      this.$refs["builder-tab"]
        .querySelector(`[data-tab="${this.tabIndex}"]`)
        .classList.add("active");
    },
    /**
     * Set current active on click
     * @param event
     */
    setActiveTab(event) {
      const tab = event.target.closest('[role="tablist"]');
      const links = tab.querySelectorAll(".nav-link");
      // remove active tab links
      for (let i = 0; i < links.length; i++) {
        links[i].classList.remove("active");
      }

      // set clicked tab index to bootstrap tab
      this.tabIndex = parseInt(event.target.getAttribute("data-tab"));

      // set current active tab
      event.target.classList.add("active");

      // keep active tab
      localStorage.setItem("builderTab", this.tabIndex);
    },
    /**
     * Set current active on click
     * @param event
     */
    setResultsTab() {
      // this.setActiveTab(event)
      this.show_component = "WidgetResults";
    },
  },
};
</script>
